.form__item--data input {
  font: inherit;
  font-size: 16px;
  line-height: 22px;
  color: $white;

  background-color: rgba($white, 0.1);
  border: none;
  cursor: pointer;
}

.form__item--text textarea {
  font: inherit;
  font-size: 16px;
  line-height: 22px;
  color: $white;

  background: rgba($white, 0.1);
  border: none;
  resize: none;
  cursor: pointer;
}

.form__item--data input::placeholder,
.form__item--text textarea::placeholder {
  color: rgba($white, 1);
}

.form__checkbox-label {
  position: relative;

  display: block;
  margin-left: 49px;

  font-size: 14px;
  line-height: 16px;
  color: $white;
  opacity: 0.8;

  cursor: pointer;
}

.form__checkbox-label--questions {
  margin-left: 40px;
}


input + .form__checkbox-label::before {
  position: absolute;
  top: 0;
  left: -49px;
  content: "";

  width: 16px;
  height: 16px;
  box-sizing: border-box;

  border: 1px solid $white;
}

input + .form__checkbox-label--questions::before {
  left: -33px;
}

input:checked + .form__checkbox-label::after {
  position: absolute;
  top: 4px;
  left: -45px;
  content: "";


  width: 16px;
  height: 16px;
  box-sizing: border-box;

  background-image: url("../img/back-icon-check.svg");
  background-repeat: no-repeat;
}

input:checked + .form__checkbox-label--questions::after {
  left: -30px;
}

.form__button {
  display: inline-block;
  vertical-align: middle;
  padding: 14px 18px;
  box-sizing: border-box;


  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  background-color: $blue;

  border: 1px solid $white;
  border-radius: 5px;
  cursor: pointer;
}

//Стайлгайд
input:hover + .form__checkbox-label::before {
  border: 1px solid $black;
}

input:focus + .form__checkbox-label::before {
  outline: 2px solid $grey-dark;
}

input:active + .form__checkbox-label::before {
  outline: none;
  opacity: 0.2;
}

.form__item input:hover,
.form__item--text textarea:hover {
  background-color: rgba($white, 0.3);
}

.form__item input:focus,
.form__item--text textarea:focus {
  outline: 2px solid $grey-dark;
}

.form__item input:active,
.form__item--text textarea:active {
  outline: none;
  background-color: rgba($white, 0.2);
}

.form__button:hover {
  border: 1px solid $black;
}


.form__button:focus {
  outline: 2px solid $grey-dark;
}

.form__button:active {
  outline: none;
  opacity: 0.5;
}
